<template>
  <div class="house-container">
    <van-dropdown-menu active-color="#3590FF">
      <van-dropdown-item
        v-model="houseType"
        :options="houseTypeOptions"
        @change="onHoustTypeChnage"
      />
      <van-dropdown-item
        v-model="chartType"
        :options="chartTypeOptions"
        @change="onChartTypeChnage"
      />
    </van-dropdown-menu>
    <section class="content-section">
      <canvas id="myChart"></canvas>
    </section>
  </div>
</template>

<script>
import F2 from '@antv/f2'
import esfTotalTsMonthList from '@/data/esfTotalTsMonth.json'
import ysfTotalTsMonthList from '@/data/ysfTotalTsMonth.json'

export default {
  data () {
    return {
      activeName: 'bar',
      houseTypeOptions: [{ text: '二手房', value: 'esf' }, { text: '一手房', value: 'ysf' }],
      chartTypeOptions: [{ text: '条形图', value: 'bar' }, { text: '折线图', value: 'line' }],
      houseType: 'esf',
      chartType: 'bar',
      chart: null
    }
  },
  mounted () {
    this.renderChart()
  },
  methods: {
    onHoustTypeChnage () {
      this.renderChart()
    },
    onChartTypeChnage () {
      this.renderChart()
    },
    renderChart () {
      if (!this.chart) {
        this.chart = new F2.Chart({
          id: 'myChart',
          pixelRatio: window.devicePixelRatio // 指定分辨率
        })
      } else {
        this.chart.clear() // 清理所有
      }

      if (this.houseType === 'esf') {
        // load the data
        this.chart.source(esfTotalTsMonthList, {
          date: {
            tickCount: 3
          }
        })
      } else {
        // load the data
        this.chart.source(ysfTotalTsMonthList, {
          date: {
            tickCount: 3
          }
        })
      }

      if (this.chartType === 'bar') {
        // draw a column chart
        this.chart.interval().position('date*value')
      } else {
        // draw a column chart
        this.chart.line().position('date*value')
      }
      this.chart.render()
    }
  }
}
</script>
<style scoped>
.house-container {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.content-section {
  max-width: 100%;
  overflow-x: hidden;
  border-radius: 4px;
}
</style>
